    color: white;
    border-radius: 0 4px 4px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.second-link{
    margin-left: 20px;
    outline: none;
    border: none;
    height: 48px;
    width: 100px;
    border-radius: 180px;
    position: relative;
    cursor: pointer;
    background: linear-gradient(50deg,#ffce32 33%, 33%, #23e5db 66%, 33%, #3a77ff  66% );
    box-shadow: 1px 3px 3px 2px rgba(0,0,0,.2);
}
.second-link span {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    color: #002f34;
    z-index: 1;
    position: relative;
    background: transparent;
    text-decoration: none;
}
.second-link::before{
    content: "";
    position: absolute;
    display: block;
    border-radius: 180px;
    width: calc(100% - 12px);
    height: calc(100% - 12px);
    top: 6px;
    left: 6px;
    background: #f7f8f8;
}
.second-link:focus{
    outline: none;
}
.second-link:hover{
    text-decoration: none;
}
.icons{
    font-size: xx-large;
    border-radius: 100px;
    margin-right: 10px;
    cursor: pointer;
    padding: 2px;
    
}
.icons:hover{
    background-color: #caf6ed;
}
