.radio-toolbar input[type="radio"] {
  opacity: 0;
  position: fixed;
  width: 0;
}

.radio-toolbar label {
    display: inline-block;
    padding: 6px 15px;
    border: 1px solid #002f34;
    border-radius: 5px;
    margin-right: 10px;
}

.radio-toolbar label:hover {
    background-color: #c7f7f5;
    border: 1px solid #00a39e;
    color: #00a39e ;
}


.radio-toolbar input[type="radio"]:checked + label {
    background-color: #c7f7f5;
    border: 1px solid #00a39e;
    color: #00a39e ;
}

